import React from 'react';
import Head from 'next/head'
import TrackVisibility from 'react-on-screen';

import Header from '../components/Header'
import Starter from '../components/index/Starter'
import Factoids from '../components/index/Factoids'
import Promises from '../components/index/Promises'
import Services from '../components/index/Services'
import Recomendations from '../components/index/Recomendations'
import Map from '../components/index/Map'
import '../css/style.scss'


class Index extends React.Component{
    constructor(props){
        super(props)

        this.state = {
            inView: 'about'
        }
    }

    render(){
        return(
            <div>
                <Head>
                    <title>Налоговый советник</title>
                </Head>

                <Header inView={this.state.inView}/>
                <TrackVisibility>
                    <Starter view={this.inView}/>
                    <Factoids/>
                    <Promises view={this.inView}/>
                    <Services view={this.inView}/>
                    <Recomendations />
                    <Map view={this.inView}/>
                </ TrackVisibility>
            </div>
        )
    }

    checkView(el){
        this.setState({
            inView: el
        })
    }
}

export default Index